import Vue from 'vue';

import Component from 'vue-class-component';
import { IsubTask } from '~/entity/task';
import { ITaskEvaluationParams } from '~/entity/route_params';
import Utils from '~/utils';
import { CACHE_NAME } from '~/cache';
import Api from '~/api';
import { Action, State } from 'vuex-class';
import { ToastMsg } from '~/entity/toast';

interface IComment {
    createTime?: string;
    txt: string;
}

@Component({})
export default class TaskEvalutionComp extends Vue {

    subTask: IsubTask = null;

    action: string = null;

    stars = [true, true, true, false, false];

    grade: number = 3;

    comment: IComment = null;

    @Action('showToast')
    showToast;

    async created() {
        const params: ITaskEvaluationParams = this.$route.params;
        this.action = params.action;

        // this.action = 'add';
        this.subTask = Utils.getItem(CACHE_NAME.subTask + params.subId);

        if (this.action === 'check') {
            try {
                // 获取具体评论
                const res = await Api.getTaskComment(params.subId);
                this.comment = {
                    createTime: res.body.obj.createTime,
                    txt: res.body.obj.comment
                };
                this.setStars(res.body.obj.grade);
            } catch (e) {
                this.showToast(new ToastMsg(e.message));
            }
        } else {
            // 添加评论
            this.comment = {
                txt: ''
            };
        }
    }

    setGrade(event: Event) {
        const target = <HTMLElement>event.target;
        const star = target.getAttribute('data-star');
        if (!star) return;

        this.grade = parseInt(star, 10) + 1;
        this.setStars(this.grade);
    }

    async addComment() {
        // 添加评论
        const txt = this.comment.txt;
        const grade = this.grade;
        const subId = this.subTask.subId;
        // 提交
        try {
            const res = await Api.addComment(subId, grade, txt);
            this.action = 'check';
            this.comment.createTime = Utils.getNowFormatDate();
        } catch (e) {
            this.showToast(new ToastMsg(e.message));
        }
    }

    /**
     * 设置得分数组
     * @param score 
     */
    setStars(score: number) {
        this.grade = score;
        switch (score) {
            case 0: 
                this.stars = [false, false, false, false, false];
                break;
            case 1:
                this.stars = [true, false, false, false, false];
                break;
            case 2:
                this.stars = [true, true, false, false, false];
                break;
            case 3:
                this.stars = [true, true, true, false, false];
                break;
            case 4:
                this.stars = [true, true, true, true, false];
                break;
            case 5:
                this.stars = [true, true, true, true, true];
                break;
        }
    }
}



